import './App.css';
import Drawer from './components/features/navigation/Drawer';
import HomePage from './components/pages/homePage/HomePage';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import { useState } from 'react';
import SignUp from './components/pages/login/SignUp';
import SignIn from './components/pages/login/SignIn';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';

function Copyright() {
  return (
    <Typography
      style={{ padding: '20px' }}
      variant='body2'
      color='textSecondary'
      align='center'
    >
      {'Copyright © '}
      <Link color='inherit' href='https://material-ui.com/'>
        yuda Bayana
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const App = () => {
  const [isSign, setIsSign] = useState(false);
  function showSignUp() {
    setIsSign(isSign ? !isSign : true);
  }
  return (
    <>
      <Router>
        <Switch>
          <Route exact path='/'>
            {isSign ? null : <Drawer showSignUp={showSignUp} />}
            {isSign ? (
              <SignUp showSignUp={showSignUp} />
            ) : (
              <HomePage showSignUp={showSignUp} />
            )}
            {isSign ? null : <Copyright />}
          </Route>
          <Route exact path='/signin'>
            <SignIn showSignUp={showSignUp} />
          </Route>
        </Switch>
      </Router>
    </>
  );
};

export default App;
